h1 {
  text-decoration: underline;
}

.background-video {
  height: 100vh;
  width: 100vw;
  object-fit: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

.background-video .desktop {
  display: none;
}

.content {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url("../public/logo_light_tiled.png");
  background-repeat: repeat;
  background-size: 200%;
  background-position: center;
  /* background-image: url("../public/logo_light_tiled.png"),
    url("../public/BCP-bg-S.png");
  background-repeat: repeat, no-repeat;
  background-size: 250%, cover;
  background-position: center, top left; */
}

.info-button {
  z-index: 10;
  margin-top: -70%;
  margin-left: 45%;
  background-color: #121212;
  color: #f1f1f1;
  display: flex;
  text-transform: uppercase;
  padding: 14px 25px;
  width: fit-content;
  height: fit-content;
}

.info-button:hover {
  cursor: pointer;
  background-color: #f1f1f1;
  color: #121212;
}

.popup {
  z-index: 10;
  width: 80vw;
  height: fit-content;
  background-color: #121212;
  opacity: 0.99;
  color: #f1f1f1;
  font-size: small;
  font-weight: 300;
  padding: 27px 25px 15px;
}

.logo {
  margin: 0 8px;
  width: 200px;
}

.nav {
  display: flex;
  justify-content: end;
}

.links {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

button {
  all: unset;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #121212;
  margin: -15px -15px 0 0;
  padding: 2px 5px;
}

.info-button:hover {
  background-color: #f1f1f1;
  color: #121212;
}

.toggle-video {
  padding: 14px 25px;
  color: #f1f1f1;
}

.icon {
  margin: 0 5px;
}

a {
  color: #f1f1f1;
}

/* Styles for larger screens */
@media (min-width: 768px) {
  .background-video {
    display: block;
    height: 100vh;
    width: 100vw;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
  }

  .background-video .mobile {
    display: none;
  }

  .content {
    background-image: url("../public/logo_light_tiled.png");
    background-repeat: repeat;
    background-size: 90%;
    background-position: center;
  }

  .info-button {
    margin-top: -5%;
    margin-left: 80%;
  }

  .popup {
    width: 400px;
    margin-left: 60%;
    margin-top: 10%;
  }
}
